.header {
    position: relative;
    left: -250px;
    transition: left 500ms;
    color: white;
}

.header a {
    text-decoration: none;
    color: rgba(250, 250, 250, 0.8);
    margin: 10px;
    font-weight: bold;
}

.header:hover {
    left: 0;
}

.header a:hover {
    color: rgba(250, 250, 250, 1);

}

.menuButton {

    height: 30px;
    width: 30px;
    border: 1px solid white;
    border-radius: 4px;
    display: inline-flex;
    color: white;
    flex-direction: column;
    justify-content: space-evenly;


}

.line {

    border: 2px solid white;
    border-radius: 2px;
    align-self: center;
    width: 90%;
}